@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Poppins', sans-serif ; 
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.App{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.upper{
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 10px;
}
@media screen and (max-width : 480px) {
  .upper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 10px;
  }
  .sort-buttons{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .sortButton{
    margin-top: 5px;
  }
 
}




.nodesContainer{
  position: absolute;
  bottom: 150px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: 40%;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.nodes {
  display: -webkit-flex;
  display: flex;
  max-width: 90%;
  min-width: 70%;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.desc{
  position: absolute;
  bottom: 50px;
}

.sort-buttons{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.sortButton {
  background-color: purple;
  color: white;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.compareButton {
  background-color: white;
  color: purple;
  border-radius: 5px;
  border: 1px solid purple;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.sortButton:hover {
  background-color: rgb(73, 0, 73);
  color: white;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.generate{
  background-color: black;
  color: white;
  border-radius: 5px;
  border: none;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
  position: absolute;
  bottom: 50px;
}

h5{
    padding: 0px;
    margin: 0px;
}
.node {
    width: 20px;
    background-color: gold;
    margin-left: 2px;
}

.node-visited {
    width: 20px;
    background-color: purple;
    margin-left: 2px;
}
