@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
* {
  font-family: 'Poppins', sans-serif ; 
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.upper{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  margin-top: 10px;
}
@media screen and (max-width : 480px) {
  .upper{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    margin-top: 10px;
  }
  .sort-buttons{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .sortButton{
    margin-top: 5px;
  }
 
}




.nodesContainer{
  position: absolute;
  bottom: 150px;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  height: 40%;
  align-items: flex-end;
}
.nodes {
  display: flex;
  max-width: 90%;
  min-width: 70%;
  justify-content: center;
  height: 100%;
  align-items: flex-end;
}
.desc{
  position: absolute;
  bottom: 50px;
}

.sort-buttons{
  display: flex;
  align-items: center;
}
.sortButton {
  background-color: purple;
  color: white;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.compareButton {
  background-color: white;
  color: purple;
  border-radius: 5px;
  border: 1px solid purple;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.sortButton:hover {
  background-color: rgb(73, 0, 73);
  color: white;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
}
.generate{
  background-color: black;
  color: white;
  border-radius: 5px;
  border: none;
  padding: 5px 10px;
  font-size: 1em;
  cursor: pointer;
  position: absolute;
  bottom: 50px;
}